.blogs-container {
  text-align: left;
  padding: 20px;
  width: 100%;
}

.blog-links {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.blog-preview {
  display: flex;
  align-items: center;
  background-color: #f4f4f4;
  padding: 10px;
  border-radius: 8px;
  text-decoration: none;
  color: inherit;
  transition: background-color 0.3s;
}

.blog-preview:hover {
  background-color: #eaeaea;
}

.blog-preview-image {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 20px;
}

.blog-preview-text {
  flex: 1;
}

.blog-preview-text h2 {
  margin: 0;
  font-size: 1.5em;
}

.blog-preview-text h3 {
  margin: 5px 0 0 0;
  color: gray;
  font-size: 1em;
}
